*{margin:0;padding:0;box-sizing:border-box}body{background:#020216;background-size:cover}.app{max-width:100%}.app .employees-tickets{margin-top:50px;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:20px}@media screen and (min-width: 992px){.app .employees-tickets{grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:50px}}

.card{background-color:rgba(50,54,63,.753);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);width:170px;padding:10px;text-align:center;margin:0 auto;border-radius:15px;box-shadow:20px 20px 22px rgba(0,0,0,.3);transition:.3s;display:flex;flex-direction:column;justify-content:space-around}.card:hover{box-shadow:20px 20px 22px rgba(0,0,0,.5)}.card__name{border-bottom:1px dashed #ffffffbc;padding-bottom:10px;color:#f0fdfffb}.card__role{margin:10px;font-style:italic;font-size:12px;color:#c0c0bff6}
.counter{margin:10px;border-radius:10px;padding:5px;background-color:rgba(99,206,206,.281);color:#55d6c9cc}.counter__name{font-size:24px}.counter__num{padding:5px;color:#f1f0ea}.counter .far{font-size:20px;padding:10px;cursor:pointer;color:#fefff7b2}.counter .far:hover{color:#ffffffec}
.header{text-align:center;padding-top:50px}.header__title{font-size:48px;margin:0;color:#e6e6e6;text-transform:uppercase}
